.kn-event-subscriber-list-item {
  padding-bottom: 0;
}

.kn-event-subscriber-list__relationship {
  align-items: center;
  min-height: var(--pf-v5-global--spacer--xl);
  margin: 0 var(--pf-v5-global--BorderWidth--md) var(--pf-v5-global--BorderWidth--lg) var(--pf-v5-global--spacer--2xl);
}

.kn-event-subscriber-list__relationship-table {
  position: relative;
  left: calc(var(--pf-v5-global--spacer--md)*-1);
  width: calc(100% + var(--pf-v5-global--spacer--xl));
}

.kn-event-subscriber-list__relationship-table:not(:last-child) {
  margin-bottom: 10px;
}

